.DragPreview {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    pointer-events: none;
    z-index: 1000;
  }
  
  .DragPreview__item {
    width: 100px;
    height: 100px;
    background: white;
    border: solid 1px black;
  }


